.projects {
    padding: 50px;
    background-color: #f5f5f5;
    text-align: center;
  }
  
  .projects h2 {
    font-size: 36px;
    color: #333333;
    margin-bottom: 40px; /* Space between heading and projects */
  }
  
  .projects-grid {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .project-card {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    max-width: 300px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    transition: transform 0.3s ease;
    padding: 15px;
    text-align: left;
  }
  
  .project-card img {
    width: 100%;
    height: auto;
  }
  
  .project-card h3 {
    font-size: 24px;
    color: #333333;
    margin: 10px 0;
  }
  
  .project-card .description {
    font-size: 16px;
    color: #555555;
    margin: 10px 0;
  }
  
  .project-card .technologies {
    font-size: 16px;
    color: #5c4226; /* Enhanced color for technologies */
    margin: 10px 0;
    font-weight: bold; /* Make it bold */
  }
  
  .project-card a {
    display: inline-block;
    margin-top: 10px;
    color: #007bff;
    text-decoration: none;
  }
  
  .project-card a:hover {
    text-decoration: underline;
  }
  
  .project-card:hover {
    transform: scale(1.05);
  }
  
  @media (max-width: 768px) {
    .projects-grid {
      flex-direction: column;
      align-items: center;
    }
  }
  